<template>
  <div class="container ido">
    <Header type="back" :title="t('index.IDO 一期')">
    </Header>
    <div class="content">
      <van-swipe class="index-swipe" indicator-color="white">
        <van-swipe-item v-for="(item,index) in dataImage[local].banner" :key="index"><img :src="item.image" alt="" /></van-swipe-item>
      </van-swipe>
      <div class="income">
        <div class="item flex-item">
          <div class="key">{{t('index.IDO待領取收益')}}</div>
          <div class="value">
            <span>{{earned}}</span>
            <div class="text" style="margin-right:15px;">Back</div>
            <div class="btn-wrap">
              <van-button size="mini" type="primary" class="btn" :loading-text="$t('index.領取')" :loading="btnProfit.loading" :disabled="!Number(earned) || btnProfit.loading" @click="takeProfitHandle()">{{$t('index.領取')}}</van-button>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="key">{{t('index.IDO已領取收益')}}</div>
          <div class="value">
            <span>{{dataOrders.receiveProfit}}</span>Back
          </div>
        </div>
      </div>
      <div class="region-title">IDO</div>
      <div class="ido-card">
        <div class="li">
          <div class="key">{{t('index.期次')}}</div>
          <div class="value">1/3</div>
        </div>
        <div class="li" v-if="isShowIdoType">
          <div class="key">{{t('index.节点')}}</div>
          <div class="value">
            <div class="input-wrap" @click="idoTypeShow = true">
              <input readonly v-model="args.groupIdoTypeName" type="text" :placeholder="t('index.請選擇幾點')">
              <img class="input-label" src="@/assets/images/arr-b.png" alt="">
            </div>
          </div>
        </div>
        <div class="li">
          <div class="key">{{t('index.支付')}}</div>
          <div class="value">
            <div class="input-wrap">
              <input v-model="args.amount" type="number" :placeholder="t('index.請輸入支付費用')" @blur="amountChange" @keyup.enter="amountChange">
            </div>
          </div>
        </div>
        <div class="li">
          <div class="key">{{t('index.獲得')}}</div>
          <div class="value"><span>{{getBack}}</span> Back</div>
        </div>
        <div class="li">
          <div class="key">{{t('index.剩餘名額')}}</div>
          <div class="value">{{remaining}}</div>
        </div>
        <van-button v-if="!Number(usdt.allowance)" type="primary" class="btn" :loading-text="$t('index.授權')" :loading="usdt.loading" :disabled="usdt.loading" @click="approveUSDT()" block>{{$t('index.授權')}}</van-button>
        <template v-if="Number(usdt.allowance)">
          <div class="btn-wrap">
            <van-button type="primary" class="btn" :loading-text="$t('index.購買')" :loading="btnIdo.loading" :disabled="btnIdo.loading" @click="idoPreHandle()" block>{{$t('index.購買')}}</van-button>
          </div>
          <div class="btn-wrap" v-if="dataOrders.isExist">
            <van-button type="primary" class="btn2" :loading-text="$t('index.購買記錄')" @click="$router.push({name:'IdoaRecord'})">{{$t('index.購買記錄')}}</van-button>
          </div>
        </template>
        <div class="tip">
          {{t('index.獲得的Back分365天每日釋放，釋放出來的Back可以隨時進行自由交易。')}}
        </div>
      </div>
      <div class="region-title">{{t('index.超級節點收益')}}</div>
      <div class="lists">
        <div class="li">
          <div class="key">
            {{t('index.我的等級')}}
          </div>
          <div class="value">
            <template v-if="dataOrders.leaderAddress === address">
              <template v-if="Number(groupFinishTime)">
                <template v-if="Number(groupIdoType) === 0">
                  {{t('index.普通節點')}}
                </template>
                <template v-if="Number(groupIdoType) === 1">
                  {{t('index.超級節點')}}
                </template>
              </template>
              <template v-else>{{t('index.隊長')}}</template>
            </template>
            <template v-if="dataOrders.leaderAddress !== address">
              <template v-if="Number(groupFinishTime)">
                {{t('index.合夥人')}}
              </template>
              <template v-else>{{t('index.普通會員')}}</template>
            </template>
          </div>
        </div>
        <div class="li">
          <div class="key">
            {{t('index.超級節點推薦收益')}}
          </div>
          <div class="value">
            <span>{{earnedIntro}}</span> Back
            <div class="btn-wrap" style="margin:0 0 0 7px">
              <van-button size="mini" type="primary" class="btn" :loading-text="$t('index.領取')" :loading="btnIntroProfit.loading" :disabled="!Number(earnedIntro) || btnIntroProfit.loading" @click="takeIntroProfitHandle()">{{$t('index.領取')}}</van-button>
            </div>
          </div>
        </div>
        <div class="li">
          <div class="key">
            {{t('index.已領取推薦收益')}}
          </div>
          <div class="value">
            <span>{{introSpotReceive}}</span> Back
          </div>
        </div>
        <div class="li" @click="$router.push({name:'IdoaPartner'})">
          <div class="key">
            {{t('index.我的團隊')}}
          </div>
          <div class="value">
            {{groupOrders}}{{t('index.個')}}
            <img class="icon" src="@/assets/images/arr-r@2x.png" alt="">
          </div>
        </div>
      </div>
    </div>
    <MyDialog v-model:isShow="isDialogSuper" :title="t('index.绑定隊長地址')">
      <template #content>
        <div class="input-wrap">
          <div class="key">{{t('index.隊長地址')}}</div>
          <input type="text" v-model="leaderAddress" :placeholder="t('index.請填寫隊長地址')">
        </div>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-ido-btn" style="margin-top:0;">
          <van-button class="btn4" type="primary" @click="bindSuperHandle(0)" :loading-text="t('index.確認成為合夥人')+'...'">{{ t("index.確認成為合夥人") }}</van-button>
          <van-button class="btn4" type="primary" @click="bindSuperHandle(1)" :loading-text="t('index.我要成為隊長')+'...'">{{ t("index.我要成為隊長") }}</van-button>
        </div>
      </template>
    </MyDialog>
    <MyDialog v-model:isShow="isDialogPrompt" :title="t('index.綁定推薦節點地址')">
      <template #content>
        <div class="input-wrap">
          <input type="text" v-model.trim="intro" :placeholder="t('index.請填寫推薦節點地址')">
        </div>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="padding-top:0;">
          <van-button class="btn4" type="primary" :loading="bindBtn.loading" :disabled="bindBtn.loading || bindBtn.disabled" @click="bindIntroHandle" :loading-text="t('index.確定')+'...'">{{ t("index.確定") }}</van-button>
        </div>
      </template>
    </MyDialog>
    <van-action-sheet v-model:show="idoTypeShow" :actions="idoTypeActions" :cancel-text="t('index.取消')" close-on-click-action @select="idoTypeChange" />
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";

import { config } from "@/utils/config";
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi.json";
import handleAbi_a from "@/utils/handleAbi_a.json";
import { Toast } from "vant";
Toast.allowMultiple();
import { getLang } from "@/utils/i18n";
import MyDialog from "@/components/MyDialog";
import { intro } from "@/storage"
import web3Data from "@/utils/initWeb3";
export default {
  components: {
    Header,
    MyDialog
  },
  setup () {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      isShowIdoType: computed(() => {
        return !data.dataOrders.isExist && (data.isToPartner || data.isBindIntro)
      }),
      local: getLang() || "zh-TW",
      dataImage: {
        'zh-TW': { banner: [{ image: require('@/assets/images/banner2_tw@2x.png') }] },
        'en': { banner: [{ image: require('@/assets/images/banner2_en@2x.png') }] }
      },
      btnProfit: {
        loading: false
      },
      btnIntroProfit: {
        loading: false
      },
      btnIdo: {
        loading: false
      },
      usdt: {
        allowance: '0',
        loading: false
      },
      usdtContract: null,
      handleContract: null,
      backContract: null,
      args: {
        amount: '',
        groupIdoType: '',
        groupIdoTypeName: ''
      },
      address0: '0x0000000000000000000000000000000000000000',
      leaderAddress: '',
      dataOrders: { amount: '', isExist: '', leaderAddress: '', receiveProfit: '0.0000', time: '', user: '' },
      remaining: '--',
      price: '0',
      getBack: '0',
      earned: '0.0000',
      isDialogSuper: false,
      groupFinishTime: '0',
      earnedIntro: '0.0000',
      introSpotReceive: '0.0000',
      groupOrders: '0',
      isDialogPrompt: false,
      intro: intro.get() || '',
      bindBtn: {
        loading: false,
        disabled: false
      },
      isToPartner: false,
      idoTypeActions: [],
      idoTypeShow: false,
      curIdoType: {},
      isBindIntro: false,
      groupIdoType: '',
      initContract: () => {
        data.usdtContract = new data.web3.eth.Contract(
          tokenAbi,
          config.usdtAddress
        );
        data.handleContract = new data.web3.eth.Contract(
          handleAbi_a,
          config.HANDLE_a
        );
        data.backContract = new data.web3.eth.Contract(
          tokenAbi,
          config.backAddress
        );
        data.initData()
      },
      initData: async () => {
        data.getAllowanceUSDT()
        await data.idoTypesFetch(0)
        await data.idoTypesFetch(1)
        await data.userOrderFetch()
        await data.isShowIdoTypeFetch()
        data.remainingGet()
        data.earnedGet()
        data.earnedIntroFetch()
        data.introSpotReceiveFetch()
      },
      getRootAddress: async () => {
        try {
          let res = await data.handleContract.methods.rootAddress().call({
            from: data.address
          });
          console.log(res, 'rootAddress')
          if (!res) {
            return Toast(t('index.頂點未設置'))
          }
          if (res !== data.address) {
            data.getIntroHandle()
          } else {
            console.log('頂點賬戶不展示綁定彈窗')
            data.idoHandle()
          }
        } catch (error) {
          proxy.$errorformat(error)
        }
      },
      getIntroHandle: async () => {
        const Toast1 = Toast.loading({
          duration: 0,
        });
        try {
          let res = await data.handleContract.methods.isBindIntro(data.address).call({
            from: data.address
          });
          console.log(res, 'isBindIntro')

          if (!res) {
            // 沒有推薦人
            if (!data.isToPartner) {
              data.isDialogSuper = true
              data.leaderAddress = ''
            } else {
              data.idoHandle()
            }
          } else {
            data.idoHandle()
          }
          Toast1.clear()
        } catch (error) {
          Toast1.clear()
          proxy.$errorformat(error)
        }
      },
      bindIntroHandle: async () => {
        data.bindBtn.loading = true
        console.log(data.intro, 'bindIntroHandle')
        try {
          if (!web3Data.web3.utils.isAddress(data.intro)) {
            Toast(t("index.邀請人地址无效"));
            data.bindBtn.loading = false
            return false
          }
          await data.handleContract.methods.bindIntro(data.intro).estimateGas({
            from: data.address
          });
          let res = await data.handleContract.methods.bindIntro(data.intro).send({
            from: data.address
          });
          console.log(res, 'bindIntro')
          data.bindBtn.loading = false
          data.isDialogPrompt = false
          data.isBindIntro = true
          Toast({
            message: t("index.Success"),
            onClose: () => {
              location.reload()
            }
          });
        } catch (error) {
          data.bindBtn.loading = false
          proxy.$errorformat(error)
          console.log(295)
        }
      },
      idoPreHandle: () => {
        data.getRootAddress()
      },
      idoHandle: async () => {
        console.log('idoHandle')
        if (!data.args.amount) {
          return Toast(t('index.請輸入支付費用'))
        }
        if (new BigNumber(data.args.amount).lte(0)) {
          return Toast(t('index.請輸入正確的支付費用'))
        }
        if (data.args.groupIdoType === '') {
          return Toast(t('index.請選擇幾點'))
        }
        data.btnIdo.loading = true
        let _amount = data.args.amount
        let leaderAddress = data.leaderAddress || data.address0
        console.log(_amount)
        try {
          let decimalsUSDT = await data.usdtContract.methods.decimals().call()
          _amount = new BigNumber(_amount).multipliedBy(10 ** decimalsUSDT).toFixed()
          console.log('_amount:', _amount, 'decimalsUSDT:', decimalsUSDT, 'leaderAddress:', leaderAddress, 'groupIdoType:', data.args.groupIdoType)
          await data.handleContract.methods.invest(_amount, leaderAddress, data.args.groupIdoType).estimateGas({
            from: data.address
          })
          await data.handleContract.methods.invest(_amount, leaderAddress, data.args.groupIdoType).send({
            from: data.address
          })
          Toast(t('Success'))
          data.args.amount = ''
          data.getBack = '0'
          data.price = '0'
          data.btnIdo.loading = false
          await data.groupIdoTypeFetch(leaderAddress)
          await data.userOrderFetch()
        } catch (error) {
          data.btnIdo.loading = false
          proxy.$errorformat(error)
          console.log(329)
        }
      },
      getAllowanceUSDT: async () => {
        try {
          let allowance = await data.usdtContract.methods
            .allowance(data.address, config.HANDLE_a)
            .call({ from: data.address });
          console.log(allowance, 'getAllowanceUSDT')
          data.usdt.allowance = allowance;
          return allowance;
        } catch (error) {
          proxy.$errorformat(error)
          console.log(342)
        }
      },
      approveUSDT: async () => {
        data.usdt.loading = true
        let decimalsUSDT = await data.usdtContract.methods.decimals().call()
        let amount = new BigNumber(50000).multipliedBy(10 ** decimalsUSDT).toFixed()
        console.log(amount, 'approve.amount')
        data.usdtContract.methods
          .approve(config.HANDLE_a, amount)
          .send({ from: data.address })
          .then(async () => {
            data.usdt.loading = false
            Toast(t("index.批准成功"));
            await data.getAllowanceUSDT();
          })
          .catch((error) => {
            console.log(error)
            data.usdt.loading = false
            Toast(t("index.批准失敗"));
          });
      },
      amountChange: () => {
        if (!Number(data.args.amount)) {
          return data.getBack = '0'
        }
        if (data.args.groupIdoType === '' && data.isShowIdoType) {
          return Toast(t('index.請選擇幾點'))
        }
        try {
          data.getBack = new BigNumber(data.args.amount).multipliedBy(data.price).toFixed()
        } catch (error) {
          proxy.$errorformat(error)
          console.log(369)
        }
      },
      userOrderFetch: async () => {
        const ToastUserOrder = Toast.loading({
          duration: 0
        })
        try {
          let userOrder = await data.handleContract.methods.userOrder(data.address).call({
            from: data.address
          })
          console.log('userOrder:', userOrder)
          if (!Number(userOrder)) {
            console.log('无订单')
          } else {
            await data.ordersFetch(userOrder)
          }
          ToastUserOrder.clear()
        } catch (error) {
          ToastUserOrder.clear()
          proxy.$errorformat(error)
          console.log(390)
        }
      },
      ordersFetch: async (orderid) => {
        const ToastOrdersr = Toast.loading({
          duration: 0
        })
        try {
          let orders = await data.handleContract.methods.orders(orderid).call({
            from: data.address
          })
          console.log('orders:', orders)
          let { amount, isExist, leaderAddress, receiveProfit, time, user } = orders
          let decimalsBACK = await data.backContract.methods.decimals().call()
          receiveProfit = new BigNumber(receiveProfit).dividedBy(10 ** decimalsBACK).toFixed(4)
          data.dataOrders = { amount, isExist, leaderAddress, receiveProfit, time, user }
          data.leaderAddress = leaderAddress
          data.partnerFetch(leaderAddress)
          data.groupFinishTimeFetch()
          await data.groupIdoTypeFetch(leaderAddress)
          data.groupIdoTypeOrderFetch(leaderAddress)
          ToastOrdersr.clear()
        } catch (error) {
          ToastOrdersr.clear()
          proxy.$errorformat(error)
          console.log(413)
        }
      },
      partnerFetch: async (leaderAddress) => {
        try {
          let getGroupOrders = await data.handleContract.methods.getGroupOrders(leaderAddress).call()
          data.groupOrders = getGroupOrders.length
        } catch (error) {
          proxy.$errorformat(error)
          console.log(421)
        }
      },
      remainingGet: async () => {
        const ToastIdoTotalAmount = Toast.loading({
          duration: 0
        })
        try {
          let idoTotalAmount = await data.handleContract.methods.idoTotalAmount().call({
            from: data.address
          })
          let idoTotalGroup = await data.handleContract.methods.idoTotalGroup().call({
            from: data.address
          })
          data.remaining = new BigNumber(idoTotalGroup).minus(idoTotalAmount).toFixed()
          console.log('idoTotalGroup:', idoTotalGroup, 'idoTotalAmount:', idoTotalAmount, 'remaining:', data.remaining)
          ToastIdoTotalAmount.clear()
        } catch (error) {
          ToastIdoTotalAmount.clear()
          proxy.$errorformat(error)
          console.log(441)
        }
      },
      groupIdoTypeOrderFetch: async (leaderAddress) => {
        try {
          let _groupIdoType = await data.handleContract.methods.groupIdoType(leaderAddress).call({
            from: data.address
          })
          data.groupIdoType = _groupIdoType
        } catch (error) {
          proxy.$errorformat(error)
          console.log(514)
        }
      },
      groupIdoTypeFetch: async (leaderAddress) => {
        const ToastgroupIdoType = Toast.loading({ duration: 0 })
        try {
          let _groupIdoType = await data.handleContract.methods.groupIdoType(leaderAddress).call({
            from: data.address
          })
          data.args.groupIdoType = _groupIdoType
          let idoTypeActions = data.idoTypeActions
          // 获取价格
          for (let k = 0; k < idoTypeActions.length; k++) {
            if (idoTypeActions[k].uint === Number(_groupIdoType)) {
              data.idoTypeChange(idoTypeActions[k])
            } else {
              idoTypeActions[k].disabled = true
            }
          }
          ToastgroupIdoType.clear()
        } catch (error) {
          ToastgroupIdoType.clear()
          proxy.$errorformat(error)
          console.log(497)
        }
      },
      idoTypesFetch: async (uint) => {
        const ToastIdoTypes = Toast.loading({ duration: 0 })
        try {
          let _idoTypes = await data.handleContract.methods.idoTypes(uint).call({
            from: data.address
          })
          let decimalsBACK = await data.backContract.methods.decimals().call()
          let decimalsUSDT = await data.usdtContract.methods.decimals().call()
          _idoTypes._idoUsdtNumber = new BigNumber(_idoTypes.idoUsdtNumber).dividedBy(10 ** decimalsUSDT).toFixed()
          _idoTypes._profit = new BigNumber(_idoTypes.profit).dividedBy(10 ** decimalsBACK).toFixed()
          _idoTypes.uint = uint
          if (uint === 0) {
            _idoTypes.name = t('index.普通節點')
          }
          if (uint === 1) {
            _idoTypes.name = t('index.超級節點')
          }
          data.idoTypeActions[uint] = _idoTypes
          console.log('idoTypes:', data.idoTypeActions)
          ToastIdoTypes.clear()
        } catch (error) {
          ToastIdoTypes.clear()
          proxy.$errorformat(error)
          console.log(497)
        }
      },
      idoTypeChange: (item) => {
        data.args.groupIdoTypeName = item.name
        data.args.groupIdoType = item.uint
        data.curIdoType = item
        data.price = new BigNumber(item._profit).dividedBy(item._idoUsdtNumber).toFixed()
        data.amountChange()
      },
      earnedGet: async () => {
        try {
          let earned = await data.handleContract.methods.earned(data.address).call()
          let decimalsBACK = await data.backContract.methods.decimals().call()
          data.earned = new BigNumber(earned).dividedBy(10 ** decimalsBACK).toFixed(4)
          console.log('earned:', earned)
        } catch (error) {
          proxy.$errorformat(error)
          console.log(472)
        }
      },
      bindSuperHandle: (index) => {
        console.log('bindSuperHandle', index)
        if (index === 0) {
          console.log('合伙人')
          if (!data.leaderAddress) {
            return Toast(t('index.請填寫隊長地址'))
          }
          data.isDialogSuper = false
          data.isToPartner = true
          data.groupIdoTypeFetch(data.leaderAddress)
        } else {
          console.log('超级节点')
          data.leaderAddress = ''
          data.isDialogSuper = false
          data.isDialogPrompt = true
        }
      },
      groupFinishTimeFetch: async () => {
        console.log(data.dataOrders.leaderAddress, 'groupFinishTimeFetch')
        if (!data.dataOrders.leaderAddress) {
          return
        }
        try {
          let groupFinishTime = await data.handleContract.methods.groupFinishTime(data.dataOrders.leaderAddress).call()
          console.log('groupFinishTime:', groupFinishTime)
          if (Number(groupFinishTime)) {
            console.log('leaderAddress是超级节点')
          }
          data.groupFinishTime = groupFinishTime
        } catch (error) {
          proxy.$errorformat(error)
          console.log(500)
        }
      },
      earnedIntroFetch: async () => {
        try {
          let earnedIntro = await data.handleContract.methods.earnedIntro(data.address).call()
          let decimalsBACK = await data.backContract.methods.decimals().call()
          earnedIntro = new BigNumber(earnedIntro).dividedBy(10 ** decimalsBACK).toFixed(4)
          console.log('earnedIntro:', earnedIntro)
          data.earnedIntro = earnedIntro
        } catch (error) {
          proxy.$errorformat(error)
          console.log(512)
        }
      },
      introSpotReceiveFetch: async () => {
        try {
          let introSpotReceive = await data.handleContract.methods.introSpotReceive(data.address).call()
          let decimalsBACK = await data.backContract.methods.decimals().call()
          introSpotReceive = new BigNumber(introSpotReceive).dividedBy(10 ** decimalsBACK).toFixed(4)
          console.log('introSpotReceive:', introSpotReceive)
          data.introSpotReceive = introSpotReceive
        } catch (error) {
          proxy.$errorformat(error)
          console.log(524)
        }
      },
      takeProfitHandle: async () => {
        console.log('takeProfitHandle')
        data.btnProfit.loading = true
        try {
          let _gas = await data.handleContract.methods.takeProfit().estimateGas({
            from: data.address
          })
          console.log('_gas：', _gas)
          await data.handleContract.methods.takeProfit().send({
            from: data.address
          })
          Toast(t('Success'))
          data.btnProfit.loading = false
        } catch (error) {
          data.btnProfit.loading = false
          proxy.$errorformat(error)
          console.log(542)
        }
      },
      takeIntroProfitHandle: async () => {
        console.log('takeIntroProfitHandle')
        data.btnIntroProfit.loading = true
        try {
          let _gas = await data.handleContract.methods.takeIntroProfit().estimateGas({
            from: data.address
          })
          console.log('_gas：', _gas)
          await data.handleContract.methods.takeIntroProfit().send({
            from: data.address
          })
          Toast(t('Success'))
          data.btnIntroProfit.loading = false
          data.introSpotReceiveFetch()
        } catch (error) {
          data.btnIntroProfit.loading = false
          proxy.$errorformat(error)
          console.log(562)
        }
      },
      isShowIdoTypeFetch: async () => {
        const ToastIsShowIdoType = Toast.loading({
          duration: 0,
        });
        try {
          let res = await data.handleContract.methods.isBindIntro(data.address).call({
            from: data.address
          });
          console.log(res, 'isBindIntro')
          data.isBindIntro = !!res
          ToastIsShowIdoType.clear()
        } catch (error) {
          ToastIsShowIdoType.clear()
          proxy.$errorformat(error)
          console.log(683)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    watch(() => data.isDialogPrompt, (val) => {
      if (!val) {
        data.leaderAddress = ''
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
